.container {
  height: auto;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  row-gap: 15px;
  column-gap: 15px;
}

.obj {
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  padding: 5px;
  background-color: rgb(242, 246, 253);
  align-items: center;
  justify-content: center;
}
